@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
body{margin:0;font-family:"Architects Daughter",cursive;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}button{border:unset}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.App{text-align:center}.App-header{background-color:#f2f2f3;min-height:95vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;font-size:calc(10px + 2vmin);color:#000}.App-header #title-logo{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;border-bottom:2px solid gray;width:100%;background:#f5f5f5;padding:10px 0 10px 0}.App-header #title-logo #app-title{font-size:1.5rem}.App-header #title-logo #app-logo{margin-right:10px}.App-header #title-logo #app-logo img{width:50px}.app-footer{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:left;justify-content:left}.app-footer #add-btn-block{background-color:#f2f2f3;position:absolute;right:10px;bottom:20px;padding:3px;border-radius:50%}.app-footer #add-btn-block #add-btn{height:50px;width:50px;border-radius:50%;background:#c8e7f5}.app-footer #add-btn-block #add-btn:focus{background:#78cef7}.app-footer .more-optns{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:left;justify-content:left;background:#fff}.app-footer .more-optns .switches{width:60%;display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;padding:5px 0 5px 0}.app-footer .more-optns .switches .q{padding:0 2px 0 2px;border-radius:20%}.app-footer .more-optns .switches .q:hover{box-shadow:-1px 13px 15px 0px rgba(185,241,245,.75)}.app-footer .more-optns .switches .q:focus{box-shadow:-1px 13px 15px 0px rgba(185,241,245,.75)}
